export default {
    data() {
        return {
            valid: false,
            userId : '',
            password : '',
            passwordChk :"",
            nickName : '',
            terms : false,
            userIdRules : [
                v => !!v || '아이디는 필수입니다.',
                v => /.+@.+/.test(v) || '아이디가 유효하지 않습니다',
            ],
            nickNameRules : [
                v=> !!v || '닉네임은 필수입니다.'
            ],
            passwordRules : [
                v => !!v || '비밀번호는 필수입니다.'
            ],
            passwordChkRules : [
                v => !!v || '비밀번호확인은 필수입니다.',
                v => v ===this.password||'비밀번호를 확인해주세요'
            ]



        }
    },
    computed : {
        me () {
            return this.$store.state.users.me;
        },
        // failedMessage() {
        //     return this.$store.state.users.failedMessage;
        // }
    },
    head (){
        return {
            title:"회원가입"
        }
    },
    methods : {
        onSubmitForm () {
            if(this.$refs.form.validate()){
                this.$store.dispatch('users/signUp',{
                    nickName : this.nickName,
                    userId : this.userId,
                    password : this.password 
                }).then((res)=> {
                    console.log("ㅋㅋㅋㅋㅋㅋㅋㅋㅋㅋㅋㅋ",res.response);
                    this.$router.push({
                    path: '/',

                    });
                }).catch(()=> {
                  alert(this.$store.state.users.failedMessage);
                });
                
            }else{
                alert('폼이 유효하지않음');
            }
            // console.log(this.valid);
        }
    },
    middleware : 'anonymous',
   watch : {
       me(value,oldValue){
           if(value){
               this.$router.push({
                   path: '/',
               })
           }
       }
   }
}
